import axios from 'axios';

const api = process.env.VUE_APP_API;

// Employess
export const apiGetEmployees = () => axios.get(`${api}/api/Employees`);
export const apiPostEmployee = (data) => axios.post(`${api}/api/Employees`, data);
export const apiPatchEmployee = (data, id) => axios.put(`${api}/api/Employees/${id}`, data);
export const apiDeleteEmployee = (id) => axios.delete(`${api}/api/Employees/${id}`);
export const apiUploadPhoto = (data) => axios.post(`${api}/api/Employees/upload-photo`, data);

// Vendors
export const apiGetVendors = () => axios.get(`${api}/api/Vendors`);
export const apiPostVendor = (data) => axios.post(`${api}/api/Vendors`, data);
export const apiPatchVendor = (data, id) => axios.put(`${api}/api/Vendors/${id}`, data);
export const apiDeleteVendor = (id) => axios.delete(`${api}/api/Vendors/${id}`);
export const apiGetVendor = (id) => axios.get(`${api}/api/Vendors/${id}`);

// WorkOrders
export const apiGetOrders = () => axios.get(`${api}/api/WorkOrders`);
export const apiPostOrder = (data) => axios.post(`${api}/api/WorkOrders`, data);
export const apiPatchOrder = (data, id) => axios.put(`${api}/api/WorkOrders/${id}`, data);

// 工單
// export const apiGetOrders = (data) => axios.get(`${api}/admin/orders`, { params: data });
export const apiGetOrder = (id) => axios.get(`${api}/admin/order/${id}`);
export const apiPatchOrderStatus = (data) => axios.patch(`${api}/admin/orderStatus`, data);
export const apiDeleteOrder = (id) => axios.delete(`${api}/admin/order/${id}`);

// 代辦事項
export const apiGetTodos = () => axios.get(`${api}/admin/todos`);
export const apiPostTodo = (data) => axios.post(`${api}/admin/todo`, data);
export const apiPatchTodo = (data) => axios.patch(`${api}/admin/todo`, data);
export const apiDeleteTodo = (id) => axios.delete(`${api}/admin/todo/${id}`);

// admin
