<template>
  <div
    class="modal fade"
    id="exampleModal"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
    ref="vendorInfo"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h1 class="modal-title fs-5" id="exampleModalLabel">
            {{ tempData.name }}
          </h1>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <div class="accordion" id="accordionExample">
            <template v-for="(item, index) in caseList" :key="index">
              <div class="accordion-item">
                <h2 class="accordion-header">
                  <button
                    class="accordion-button"
                    :class="{ collapsed: index !== 0 }"
                    type="button"
                    data-bs-toggle="collapse"
                    :data-bs-target="`#collapse${index}`"
                    aria-expanded="true"
                    aria-controls="collapseOne"
                  >
                    {{ item.title }} #{{ index + 1 }}
                    <span
                      class="ms-2 text-success"
                      v-if="item.isDone"
                      >已完成</span
                    >
                    <span
                      class="ms-2 text-danger"
                      v-else
                      >未完成</span
                    >
                  </button>
                </h2>
                <div
                  :id="`collapse${index}`"
                  class="accordion-collapse collapse"
                  :class="{ show: index === 0 }"
                  data-bs-parent="#accordionExample"
                >
                  <div class="accordion-body">
                    {{ item.content }}
                  </div>
                </div>
              </div>
            </template>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Modal } from 'bootstrap';
import { apiGetVendor } from '@/assets/javascript/api';

export default {
  data() {
    return {
      caseList: [
        {
          title: '自治',
          isDone: true,
          date: '2024/01/02',
          content:
            'hidden by default, until the collapse plugin adds the appropriateclasses that we use to style each element. These classes control the overall appearance, as well as the showing and hiding via CSS transitions. You can modify any of this with custom CSS or overriding our default variables. Its also worth noting that just',
        },
        {
          title: '中正',
          isDone: true,
          date: '2024/03/02',
          content:
            'hidden by default, until the collapse plugin adds the appropriateclasses that we use to style each element. These classes control the overall appearance, as well as the showing and hiding via CSS transitions. You can modify any of this with custom CSS or overriding our default variables. Its also worth noting that just',
        },
        {
          title: '市政',
          isDone: false,
          date: '2024/04/02',
          content:
            'hidden by default, until the collapse plugin adds the appropriateclasses that we use to style each element. These classes control the overall appearance, as well as the showing and hiding via CSS transitions. You can modify any of this with custom CSS or overriding our default variables. Its also worth noting that just',
        },
        {
          title: '文山',
          isDone: true,
          date: '2024/05/02',
          content:
            'hidden by default, until the collapse plugin adds the appropriateclasses that we use to style each element. These classes control the overall appearance, as well as the showing and hiding via CSS transitions. You can modify any of this with custom CSS or overriding our default variables. Its also worth noting that just',
        },
      ],
      infoModal: '',
      tempData: {},
    };
  },
  methods: {
    getVendor(id) {
      this.$emitter.emit('loadingStatus', true);
      apiGetVendor(id)
        .then((res) => {
          this.tempData = res.data;
          this.$emitter.emit('loadingStatus', false);
        })
        .catch((err) => {
          console.log(err);
          this.$emitter.emit('loadingStatus', false);
        });
    },
  },
  mounted() {
    this.infoModal = new Modal(this.$refs.vendorInfo);
    this.$emitter.on('infoVendor', (e) => {
      this.getVendor(e);
      this.infoModal.show();
    });
  },
};
</script>
